html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.circle {
  height: 8vh;
  width: 8vh;
  opacity: .8;
  background: red;
  border-radius: 10vh;
  margin: auto;
  position: absolute;
  top: 80%;
  bottom: 0;
  left: 0;
  right: 0;
}

.circle:before {
  content: "";
  width: 0;
  height: 0;
  border: 2vh solid #0000;
  border-left: 4vh solid #fff;
  border-right-width: 0;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: -5px;
}

.circle:active {
  opacity: 1;
}

